.App {
  text-align: center;
}


.container {
  width:85.6vw;
  height:100vh;
  margin-left:14.4vw;
  background:Red;
  overflow-x:hidden;
  position:absolute;
  top:0;
  left:0;
}

.nav {
  background:#333;
  display:flex;
  flex-direction: column;
  width:15vw;
  height:100vh;
  position:absolute;
  top:0;
  left:0;
  justify-content: space-around;
  align-items:center;
  font-size:24px;
  /* transform:translateX(-15vw); */
  overflow:scroll;
  overflow-x:hidden;
  position:relative;
}

.nav img {
   position:absolute;
  top:0;
  left:0; 
  width:100%;
  margin-bottom:10vh;
}

.nav a {
  text-decoration: none;
  font-weight:100;
  color:white;
}

.nav .icon {
  /* width:32px; */
  /* height:32px; */
  position:absolute;
  top:0;
  left:50vw;
}

.idle1 img {
  /* display:flex; */
  justify-content: center;
  align-items: center;
  width:256px;
  height:256px;
  position:absolute;
  top:calc(50vh - 128px);
  left:calc(42.3vw - 128px);
  /* margin:0 auto; */
  border-radius:50%;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.75);
  cursor:pointer;
  animation: spin 60s linear infinite;
}



.idle1 h2, h1 {
  text-align: center;
  color:#333;
  font-weight:200;
  transition:all 250ms;
}

h1 {
  animation: yeet 10ms linear infinite;
  position:absolute;
  top: 0;
  left:0;
  color:white;
}

.wtf {
  animation: wtf 250s linear infinite;
  animation-direction: alternate;
}

@keyframes wtf {
  from {
    width:10px;
  }
  to {
    width:1920px;
  }
}

@keyframes yeet {
  from {
    opacity:1;
    transform:translateY(0)
  }
  to {
    opacity:0;
    transform:translateY(-4vh)
  }
}

@keyframes spin {
  from {
    transform:rotate(0deg)
  }
  to {
    transform: rotate(360deg);
  }
}